import { combineReducers } from "redux";

import collectionWord from "./collection-word/reducer";
import simpleWord from "./simple-word/reducer";
import teacherQuestion from "./teacher-question/reducer";
import kekeTrain from "./keke-train/reducer";
const reducer = combineReducers({
  collectionWord,
  simpleWord,
  teacherQuestion,
  kekeTrain
});
export default reducer;
